<template>
  <div class="sub">
    <SubTitle v-bind:titledata = "title"></SubTitle>
    <!--  -->
    <div class="program school">
      <div class="contents">
        <h2>  2, 1  <br>
               </h2>
        <p class="par">     ()  .<br>
               ()  .<br>
                 <br>
                 .</p>
        <p class="par">, , , <br>
                  <br>
                 <br>
            ()   .</p>
        <!-- <p class="process">SCHOOL PROCESS</p> -->
        <ul class="schoolMenu">
          <li v-bind:key="item.text" v-for="item in menu" >
            <router-link :to="item.url">{{item.text}}</router-link>
          </li>
        </ul>
        
        <router-view></router-view>
        
      </div>
    </div>
  </div>
</template>

<script>
import SubTitle from '../Sub/SubTitle'
export default {
  name: 'School',
  components : {
    SubTitle
  },
  data : function() {
return {
    title : "PROGRAM > SCHOOL PROCESS",
    menu : [
          {text : "", url : "/program/school/vision"},
          {text : "", url : "/program/school/paradigm"},
          {text : "", url : "/program/school/economic"},
          {text : "", url : "/program/school/startup"}
      ]
  };
}
}
window.onload = function(){
  const ul = document.querySelector('.schoolMenu');
  const li = ul.querySelectorAll('li');
  li[0].classList.add('active');
  li.forEach(function(e){
    e.addEventListener('click', function(){
      for(var i=0; i<li.length; i++){
        li[i].classList.remove('active');
      }
      this.classList.add('active');
    })
  })
}

</script>
  
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .schoolMenu li{
    width: 25%;
    float: left;
    text-align: center;
    font-size: 15px;
    color: #3642c6;
    height: 30px;
    line-height: 30px;
  }

  .schoolMenu li.active{
    background-color: #3642c6;
    color: #c4f580;
  }

  .schoolMenu li a{
    display: block;
    width:100%;
  }

  .schoolMenu{
    border-bottom: 2px solid #3642c6;
  }

  .schoolMenu::after{
    display: block;
    clear: both;
    content:'';
  }
</style>
